import "./styles/Home.css";
import userIcon from "../images/profile.png";

import { createRecord, fetchAllRecords } from "./Utils";
import { useState, useEffect } from "react";
import RefreshToken from "./RefreshToken";
import Records from "./Records";
// import Button from "react-bootstrap/Button";

export default function Home() {
  //storing list of records received from DB
  const [records, setRecords] = useState("");

  //boolean to check if there is active token
  const [expiredToken, setExpiredToken] = useState(false);

  useEffect(() => {
    fetchAllRecords(setExpiredToken, setRecords);
  }, [expiredToken]);
  return (
    <div className="AppWrapper">
      {/* <Button
        size="sm"
        onClick={() => {
          createRecord(token, setActiveToken, setRecords);
        }}
      >
        Create Record
      </Button> */}
      <header className="headerWrapper">
        <div className="headerUser">
          <div className="headerUserIcon">
            <img className="userIcon" src={userIcon} alt="User Icon"></img>
          </div>
          <div className="headerUserName">
            <p>Cristi Nita</p>
          </div>
        </div>
        <div className="">2</div>
      </header>
      <div className="contentWrapper">
        <div className="content1Wrapper">1</div>
        <div className="content2Wrapper">
          <div>{records && <Records records={records} />}</div>
        </div>
      </div>
      <div className="tokerWrapper">
        <div>
          {expiredToken && <RefreshToken setExpiredToken={setExpiredToken} />}
        </div>
      </div>
    </div>
  );
}
