import { useRef } from "react";
import Button from "react-bootstrap/Button";
import { updateToken } from "./Utils";

export default function RefreshToken({ setExpiredToken }) {
  const inputToken = useRef();

  return (
    <div>
      <div>
        <input type="text" ref={inputToken}></input>
        <Button
          size="sm"
          onClick={() => {
            updateToken(inputToken.current.value, setExpiredToken);
          }}
        >
          Refresh token
        </Button>
      </div>
    </div>
  );
}
